import React, { Component } from 'react';

const numbers = Array.from({ length: 5 }, (_, i) => i + 1);
class Rating extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     activeIndex: null,
                     hoverIndex: null,
              };
       }
       handleClick = (index) => {
              const {  addRatings } = this.props;
              this.setState({ activeIndex: index });
              addRatings(index)
       };

       handleMouseEnter = (index) => {
              this.setState({ hoverIndex: index });
       };

       handleMouseLeave = () => {
              this.setState({ hoverIndex: null });
       };
       render() {
              const { hoverIndex, activeIndex } = this.state
              return (
                     <div className='t_rate_wrap'>
                            {numbers.map((number, index) => (<div key={index} className={`t_star_ico_wrap${number <= hoverIndex ? ' t_h_active_' : ''}${number <= activeIndex ? ' t_active_' : ''}`}
                                   onClick={() => this.handleClick(number)}
                                   onMouseEnter={() => this.handleMouseEnter(number)}
                                   onMouseLeave={this.handleMouseLeave}
                            ><div className='t_star_ico tb__icon tb-star-outline'><div></div></div></div>))}
                     </div>
              );
       }
}

export default Rating;