import { AUTH_TOKEN, isTagbox, isTagShop, Pwidget, reelsPlay, tagembed, tbox, test, wUrl } from "../actions/api";
import { WebUrl } from "../constants";
import store from "../store";

let reelPlayTime = [];
let playAdded = false;

const headers = new Headers({
    'Content-Type': 'application/json',
    'Authorization': `Bearer ${AUTH_TOKEN}`,
    'Productdomain': tbox
});


export const tb_onsite_btn_disabled = (a) =>
    document.getElementById("tb_onsite_btn_id")?.classList.toggle("tb_onsite_btn_disabled", a === 1);


export const onWindowEvent = (s) => ({
    1: () => window.onShowDemoPost(1),
    2: () => window.hendleFeeds("addFeeds"),
    3: () => window.hendleFeeds("manageFeeds")
}[s]?.());

export const formatUnixTimestamp = (timestamp) => {
    const date = new Date(timestamp * 1000);
    const monthNames = [
        'January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'
    ];
    return `${monthNames[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`;
}

export const generateUniqueString = () => Math.floor(Math.random() * 10000).toString().padStart(4, '0');

export const reelPlayTimeUpdateData = (time) => {
    try {
        reelPlayTime.push(time)
    }
    catch (ex) {
        console.log(ex)
    }
}

export const isValidAuthorImage = (url) => url && url !== "" && !url.includes('ui-avatars');

export const bytesToSize = (b) => {
    const units = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
    if (!b) return '0 Byte';
    const i = Math.floor(Math.log(b) / Math.log(1024));
    return { size: Math.round(b / Math.pow(1024, i), 2), sizeText: units[i] };
};


export const ensureFullyQualifiedUrl = (url) =>
    url.startsWith('http://') || url.startsWith('https://') ? url : 'https://' + url;

export const timeAgo = (timestamp) => {
    const diff = Date.now() - timestamp * 1000;
    const units = [
        { label: "year", ms: 31536000000 },
        { label: "month", ms: 2592000000 },
        { label: "week", ms: 604800000 },
        { label: "day", ms: 86400000 },
        { label: "hour", ms: 3600000 },
        { label: "minute", ms: 60000 }
    ];

    if (diff < 60000) return "just now";

    const unit = units.find(({ ms }) => diff >= ms);
    const value = Math.floor(diff / unit.ms);
    return `${value} ${unit.label}${value > 1 ? "s" : ""} ago`;
}


export const STRING_TO_URL_CONVERT = (string) => {
    try {
        if (!string?.includes("http")) return string;
        return [...new Set(string.match(/(((ftp|https?):\/\/)[\w@:%_\+.~#?&//=]+)/g))].reduce(
            (result, url) =>
                result.replaceAll(
                    url,
                    `<a class="text_links_" role="link" target="_blank" href="${url}">${url}</a>`
                ),
            string
        );
    } catch {
        return string;
    }
};

export const brandingUrl = () => [`${WebUrl}`, 'Website Widget'];
export const GET_MEDIA_NAME = (p) => p && String(p) ? unescape(p).split('/').pop().split("/").slice(-1).join().split(".").shift() : '';


export const mainClass = !["localhost", "widget", "wall_id="].every((key) => wUrl(key))
