import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux'; 
import Rating from './Components/Rating';
import { onsiteUpload } from '../../../../utils';
import './review-hub.scss'
import { CloudUrl } from '../../../../constants';
import { WebUrl } from '../../../../constants';
import { isSocialWall, isTagbox, isTagShop, isUgcsuit } from '../../../../actions/api';
import { tb_onsite_btn_disabled } from '../../../../utils/Common';
import { onSitePopup, onsiteTokenUpdate, submitPostData } from '../../../../actions/OtherActions';
const Uploads = React.lazy(() => import('./Components/Uploads'));

const validateEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

const Alert = ({ wall, type, onClosePopUp }) => {
    return <div className="t_modal_content">
        <div className='t_s_wrap'>
            <img className='t_s_icon_' src={`${CloudUrl}/media/icons/${type}-icon.svg`} height={80} width={80} alt='' />
            {type == "check" ? <div className="t_s_title">Thanks for Your Efforts!</div> : null}
            <div className="t_s_desc">{wall?.SnapUp?.Form.status ? wall?.SnapUp?.Form.successText : `Thank you for sharing your amazing content with us.`}</div>
            <div className='t_f_close_wrap'>
                <button className='t_f_p_btn' onClick={onClosePopUp}>Close</button>
            </div>
        </div>
    </div>
}

const Logo = ({logo}) => {
    return logo ? <div className='t_m_logo_w'><img className='t_m_logo' src={logo} height={20} width={130} alt='' /></div> : null
}
class ReviewHub extends Component {

    constructor(props) {
        super(props);
        this.state = {
            submitPost: true,
            isOpen: true,
            step: 1,
            wall_id: null,
            name: '',
            email: '',
            upload_image: [],
            tandc: true,
            nameError: false,
            emailError: false,
            submitErrorMessage: null,
            apiRequest: false,
            isActiveIndex: 0,
            isAddMoreActive: false, 
            submitError: false,
            is_terms_check: 0,
            errorClickMore: false,
            uploadError: '',
            rateProduct: 0,
            reviewTitle: '',
            review: '',
            rateProductError: false,
            reviewTitleError: false,
            captionError: false,
            layout: props?.wall?.SnapUp?.Form.type || 1,
            removedFile:{}
        }
    }

    handleNext = event => this.setState({ submitPost: false }) 

    onShowCloseSubmitPicPopUp = event => {
        const { isOpen } = this.state;
        this.setState({ isOpen: !isOpen })

    }
    updateImageWithData = (data) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length < 20) {
            upload_image.push(data);
            this.setState({ upload_image })
        }
    }


    onClickToaddMore = event => {
        const { upload_image } = this.state; 

        if (upload_image && upload_image.length < 20) { 
            upload_image.push({ file: null, caption: null, review: '', rating: 0, error: false, type: 1, thumb_file: "", videoPath: "" })
            this.setState({ isAddMoreActive: true, upload_image, isActiveIndex: upload_image.length == 0 || upload_image.length == 1 ? 0 : parseInt(upload_image.length) - 1, step: 1,  submitError: false, errorClickMore: false })
        }
        else if (upload_image.length >= 20) this.setState({ errorClickMore: true })


    }

    onClickToUpdateMoreStep = (step) => event => {
        const { isAddMoreActive, isActiveIndex, upload_image } = this.state;
        if (isAddMoreActive && upload_image[isActiveIndex])  upload_image.splice(isActiveIndex, 1);
        this.setState({ isAddMoreActive: false, upload_image, step, isActiveIndex: upload_image && upload_image.length > 0 && (parseInt(upload_image.length) - 1) > 0 ? (parseInt(upload_image.length) - 1) : 0, errorClickMore: false })
    }


    onSubmitPostData = event => {
        const { name, email, upload_image, is_terms_check, tandc, rateProduct, reviewTitle, review, layout, captionError } = this.state;
        const { wallId, modalPop, wall, feeds } = this.props;
        this.setState({ submitError: false })
        if (tandc && (name?.length > 0) && (email?.length > 0 && validateEmail(email) && !email.includes("mailinator")) && tandc && ((layout == 1 && rateProduct > 0 && (isTagShop ? reviewTitle : true)) || layout == 2) && review?.length > 0 && !captionError) {
            this.setState({ apiRequest: true })
            var ugcSuite = window.ugcSuite ? true : isUgcsuit ? true : false
            var feedData = {}
            if (ugcSuite) {
                if (feeds && feeds.length > 0) {
                    feeds.map((fItem) => {
                        if (fItem && fItem.Network && Object.keys(fItem.Network).length && fItem.Network.id == 29 && fItem.value1) feedData = { feedId: fItem.value1 }
                    })
                }
            }
            let upload_files = []
            if (upload_image && upload_image.length > 0) {
                upload_image.map((itmFile) => upload_files.push({ ...itmFile })  )
            }
            else {
                upload_files = [{
                    video: "",
                    type: 1,
                    height: "",
                    width: "",
                    thumbnail: ""
                }]
            }

            const postData = { wallId: wall?.ThemeInfo.wallId ? wall?.ThemeInfo.wallId : wallId, name, email, media: upload_files, ONSITE_TOKN: modalPop.ONSITE_TOKN, ...feedData, caption: review, rating: rateProduct, title: layout == 2 ? " " : reviewTitle }
            if ([104746].includes(wall?.ThemeInfo.ownerId))  postData.is_terms_check = is_terms_check

            submitPostData(postData).then((response) => {
                const { ONSITE_TOKN } = response.data;
                this.props.onsiteTokenUpdate(ONSITE_TOKN)
                this.setState({ step: 3, apiRequest: false }, () => setTimeout(() => {
                    if (onsiteUpload) window.location.reload()
                    else this.props.onSitePopup(false)
                }, 5000), document.querySelector('html').style.overflow = "auto")
            })
                .catch((error) => {
                    const { message } = error.toJSON();
                    if (message) this.setState({ submitError: true, submitErrorMessage: message, apiRequest: false })
                    console.error(error);
                });
         }
        else {
            if (this.state.review == '' || this.state.review.trim().length == 0) this.setState({ captionError: true, apiRequest: false })
            if (this.state.name == null || this.state.name.trim().length == 0) this.setState({ nameError: true, apiRequest: false })
            if (this.state.email == null || this.state.email.trim().length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true, apiRequest: false })
            if (rateProduct == 0) this.setState({ rateProductError: !isSocialWall ? true : false })
            if (this.state.reviewTitle == null || this.state.reviewTitle.trim().length == 0) this.setState({ reviewTitleError: true, apiRequest: false })
        }
    }


    addCaption = event => {
        if (String(event.target.value).length <= 500) this.setState({ review: event.target.value, captionError: false })
        else this.setState({ captionError: true })
    }

    addReview = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].review = event.target.value;
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].error = false;
            this.setState({ upload_image })
        }
    }


    addRatings = (rating) =>  this.setState({ rateProduct: rating, rateProductError: false })
    onChangeReviewTitle = event =>  this.setState({ reviewTitle: event.target.value, reviewTitleError: false })
    

    addPostImage = (file, thumb_file, type, mediaHeight, mediaWidth) => {
        let { upload_image } = this.state;
        const uploadDetails = {
            video: file,
            type: type,
            height: mediaHeight,
            width: mediaWidth,
            thumbnail: thumb_file
        }
        upload_image.push(uploadDetails);
        if(upload_image.length > 20) upload_image.length = 20
        this.setState({ upload_image, isActiveIndex: upload_image.length + 1 })
    }

    onClickToRemove = (item) =>  {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            const newImages = upload_image.filter(file => file.video != item.video)
            this.setState({ upload_image: newImages, errorClickMore: false, isActiveIndex: newImages.length + 1, removedFile:item })
        }
    }


    onClickToImageRemove = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].file = null;
            upload_image[activeIndex].thumb_file = "";
            upload_image[activeIndex].videoPath = "";
            upload_image[activeIndex].type = 1;
            upload_image[activeIndex].ext = null;
            upload_image[activeIndex].size = 0;
            upload_image[activeIndex].fileName = null;
            upload_image[activeIndex].mediaHeight = 0;
            upload_image[activeIndex].mediaWidth = 0;
            this.setState({ upload_image })
            tb_onsite_btn_disabled(2)
        }
    }

    onClickToEdit = (activeIndex) => event =>   this.setState({ isActiveIndex: activeIndex, step: 1 })
  
    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }

    onTextEmail = event => {
        this.setState({ email: event.target.value, emailError: false }, () => {
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
        })
    }

    onUpdatetandc = event =>  this.setState({ tandc: !this.state.tandc })

    onClosePopUp = () => {
        this.props.onSitePopup(false)
        document.querySelector('html').style.overflow = "auto";
    }

    termsCheck = (event) => this.setState({ is_terms_check: event })

    uploadErrorCallback = (error) => {
        this.setState({
            uploadError: error
        }, () => {
            setTimeout(() => {
                this.setState({ uploadError: '' })
            }, 5000)
        })
    }

    backStepFun = () => this.setState({  step: 1   })

    render() {
        const { isActiveIndex, step, upload_image, isAddMoreActive, name, email, tandc, nameError, emailError, apiRequest, submitError, submitErrorMessage, errorClickMore, uploadError, reviewTitle, review, rateProductError, reviewTitleError, captionError, layout, removedFile } = this.state;
        const { wall, modalPop, isFreeAdsStatus } = this.props;
        const { Form } = wall.SnapUp

        const backgroundStyle = {
            backgroundColor: Form?.background ? Form?.background : null,
            backgroundImage: Form?.background.url ? `url(${Form?.background.url})` : null,
        }
        const { onClosePopUp } = this;
        return <div className={`t_modal${isFreeAdsStatus ? ' t_brand_' : ''}`}>
            <div className='t_modal_wrap' style={backgroundStyle}>
                {(Object.keys(wall?.SnapUp || {}).length && Form.status) ?
                    <div className='t_modal_container'>
                        {step == 3 ?
                            <Alert wall={wall} onClosePopUp={onClosePopUp} type={"check"} />
                            :
                            <div className='t_modal_content'>
                                <Logo logo={Form?.logo.url}/>
                                <div className="t_m_close_btn tb__icon tb-close-alt" onClick={onClosePopUp}><div></div></div>
                                {Form.title ? <div className='t_m_title'>{Form.title}</div> : null}
                                {Form.subTitle != '' ? <div className='t_m_subtitle'>{Form.subTitle}</div> : null}
                                {layout == 1 ? <div className='t_f_group'>
                                    <Rating isActiveIndex={isActiveIndex} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} isAddMoreActive={isAddMoreActive} item={upload_image[isActiveIndex]} addRatings={this.addRatings} />
                                    {rateProductError ? <div className='t_f_error t_text_center'> * Rating is required.</div> : null}
                                </div> : null}
                                {layout == 1 && isTagShop ? <div className='t_f_group'>
                                    <div className='t_label_'>Review Title <div className='t_t_danger'>*</div></div>
                                    <input type="text" className={`t_from_input ${reviewTitleError ? 't_f_invalid' : ''}`} placeholder='Enter review title' onChange={this.onChangeReviewTitle} value={reviewTitle} />
                                    {reviewTitleError ? <div className='t_f_error'> * Review Title is required.</div> : null}
                                </div> : null}
                                <div className='t_f_group'>
                                    <div className='t_label_'>{layout == 2 ? `Caption` : `Review`}  <div className='t_t_danger'>*</div></div>
                                    <textarea className={`t_from_textarea ${captionError ? 't_f_invalid' : ''}`} placeholder={`Write your ${layout == 2 ? `caption` : `review`} here`} value={review}
                                        onChange={this.addCaption} />
                                    {captionError ? <div className='t_f_error'> * {String(review).length <= 0 ? `${layout == 2 ? `Caption` : `Review`} is required.` : `${layout == 2 ? `Caption` : `Review`} limit of 500 exceeded!`}</div> : null}
                                </div>
                                <div className='t_f_group'>
                                    <div className='t_label_'>Picture/Video (optional)</div>
                                    <div className='t_f_upload'>
                                        {(upload_image && upload_image.length > 0) ?
                                            upload_image && upload_image.map((item, index) => item.thumbnail ? <div key={index} className='t_m_up_img_wrap' onClick={this.onClickToEdit(index)}>
                                                {item.thumbnail ?
                                                    <div className='t_m_up_img_in'>
                                                        <img className='t_m_up_img' src={item.thumbnail} height={80} width={80} alt='' />
                                                        { [3,5].includes(item.type) ? <div className="t_m_vi_ico tb__icon tb-play"><div></div></div> : null}
                                                    </div>
                                                    : null}
                                                <div className="t_m_remove_btn tb__icon tb-close-alt" onClick={()=>this.onClickToRemove(item,index)}><div></div></div>
                                            </div> : null)
                                            : null}
                                        <Suspense fallback={<div className='t_m_upload'>Loading...</div>}>
                                            <Uploads updateImageWithData={this.updateImageWithData} isActiveIndex={isActiveIndex} addPostImage={this.addPostImage} ONSITE_TOKN={modalPop.ONSITE_TOKN} uploadErrorCallback={this.uploadErrorCallback} upload_image={upload_image} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} removedFile={removedFile}/>
                                        </Suspense>
                                    </div>
                                    {uploadError ? <div className='t_f_error'>{uploadError}</div> : null}

                                </div>
                                <div className='t_row_'>
                                    <div className='t_col_'>
                                        <div className='t_f_group'>
                                            <div className='t_label_'>Name  <div className='t_t_danger'>*</div></div>
                                            <input type="text" className={`t_from_input ${nameError ? 't_f_invalid' : ''}`} placeholder='Enter Your name' onChange={this.onTextName} value={name} />
                                            {nameError ? <div className='t_f_error'> Name is required.</div> : null}
                                        </div>
                                    </div>
                                    <div className='t_col_'>
                                        <div className='t_f_group'>
                                            <div className='t_label_'>Email  <div className='t_t_danger'>*</div></div>
                                            <input type="text" className={`t_from_input ${emailError ? 't_f_invalid' : ''}`} placeholder='Enter you email' onChange={this.onTextEmail} value={email} />
                                            {emailError ? <div className='t_f_error'> {String(email).trim().length == 0 ? "Email is required." : !validateEmail(email) ? "Enter valid email." : ""}</div> : null}
                                        </div>
                                    </div>
                                </div>
                                {[104746].includes(wall.ThemeInfo.ownerId) ?
                                    <div className='t_f_check_inline'>
                                        <input type="checkbox" id="t_accept" className='t_from_check' onChange={(e) => this.termsCheck(e.target.checked ? 1 : 0)} />
                                        <div className='t_f_label_wrap'>
                                            <label className='t_check_label_' htmlFor="t_accept">I agree to CALM using my story for marketing purposes --</label>
                                            <a href={Form.termsLink} className='t_tc_link' target='_blank'>click for more information on how your story will be used.</a>
                                        </div>
                                    </div>
                                    :
                                    <div className='t_f_group'>
                                        <div className='t_f_check_inline'>
                                            <input type="checkbox" id="t_accept" className='t_from_check' value={'accept'} onChange={this.onUpdatetandc} checked={tandc} />
                                            <label className='t_check_label_' htmlFor="t_accept">By clicking on Submit, you are agreeing to our <a href={`${WebUrl}${isTagbox ? 'on-site-uploads-content-rights/' : 'snapup-content-rights/'}`} className='t_tc_link' target='_blank'>terms and conditions</a> *</label>
                                        </div>
                                        {tandc == false ? <div className="t_f_error" >* Please agree to the terms and conditions to submit your media.</div> : null}
                                    </div>
                                }
                                {submitError ?
                                    <div className='t_f_group'>
                                        <div className='t_f_error'>* {submitErrorMessage ? submitErrorMessage : `Please check complete details.`}</div>
                                    </div>
                                    : null}
                                <div className='t_f_group t_text_center'>
                                    <div className={`t_f_p_btn ${apiRequest ? ` t_f_btn_spinner` : ``}`} onClick={this.onSubmitPostData}>{Form.button.text}</div>
                                </div>
                                {step == 2 ? <div className='t_f_group t_text_center'>
                                    <div className={`t_f_s_btn`} onClick={this.backStepFun}>Back</div>
                                </div> : null}
                                {errorClickMore ? <div className='t_f_error'>* Max 20 posts are allowed.</div> : null}
                            </div>}
                    </div>
                    : null}
            </div>
        </div >

    }
}
const mapStateToProps = state => {

    if (state.modalPop != undefined && state.modalPop.onSiteIsShowPopUp == true)  document.querySelector('html').style.overflow = "hidden";

    return {
        modalPop: state.modalPop
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSitePopup: (status) => dispatch(onSitePopup(status)),
        onsiteTokenUpdate: (token) => dispatch(onsiteTokenUpdate(token)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReviewHub);
